import { FC } from 'react'
import { Key } from '../../../config/componentTypes'
import './a-input.scss'

interface IProps {
    inputConfig: {
        style: Record<Key, any>,
        placeholder: string | undefined
    } 
}


const AInput: FC<IProps> = ({ inputConfig }) => {

    const { style, placeholder } = inputConfig

    return (
        <input style={style} 
        placeholder={placeholder} 
        className="a-input">
        </input>
    )
}
export default AInput