import { AxiosResponse } from 'axios'
import { Widget } from '../config/componentTypes'
import request from './request'
import { storage } from '../utils'

type Key = Exclude<keyof any, symbol>

interface Response<T = any> {
    status: number,
    data: T,
    message: string
}

/**/
export const userId = storage.get('userId')


/**/
export const login = (userName: Key, password: Key) => {
    return request.get('/login', {
        userName,
        password
    }) as Promise<AxiosResponse<Response>>
}

/**/
export const register = (userName: Key, password: Key) => {
    return request.post('/register', {
        userName,
        password
    }) as Promise<AxiosResponse<Response>>
}


export const addPage = (
    pageInfo: {
        pageUrl: string,
        pageName: string,
        description: string,
        componentData: Widget[]
    }
) => {
    const token = storage.get('token')
    return request.post('/addpage', {
        pageInfo,
        token
    }) as Promise<AxiosResponse<Response>>
}

export const getPages = () => {
    const token = storage.get('token')
    return request.get('/getpages',
        { token }
    ) as Promise<AxiosResponse<Response>>
}

export const delPage = (pageId: string) => {
    const token = storage.get('token')
    return request.post('/delpage',
        { token, pageId }
    ) as Promise<AxiosResponse<Response>>
}

export const saveData = (componentData: Widget[], pageId: string) => {
    console.log(pageId)
    const token = storage.get('token')
    return request.post('/save',
        { token, componentData, pageId }
    ) as Promise<AxiosResponse<Response>>
}

export const getComponentData = () => {
    const token = storage.get('token')
    const pageId = storage.get('pageId')
    return request.get('/getdata',
        { token, pageId }
    ) as Promise<AxiosResponse<Response>>
}

export const publishProject = () => {
    const token = storage.get('token')
    return request.post('/publish',
        { token }
    ) as Promise<AxiosResponse<Response>>
}

export const genertaeCode = () => {
    const token = storage.get('token')
    return request.post('/generate',
        { token }
    ) as Promise<AxiosResponse<Response>>
}

export const generateJSON = () => {
    const pageId = storage.get('pageId') as string
    const token = storage.get('token')
    return request.get('/generate_json',
        { token, pageId }
    ) as Promise<AxiosResponse<Response>>
}