import { FC } from 'react'
import { Key } from '../../../config/componentTypes'
import './a-button.scss'

export interface ButtonConfig {
    propValue: string | number
    style: Record<Key, any>
    title: string | undefined
}

const AButton: FC<{ buttonConfig: ButtonConfig }> = ({ buttonConfig }) => {
    const { propValue, style, title } = buttonConfig

    return (
        <button title={title} style={style} className="a-button">
            {propValue}
        </button>
    )
}
export default AButton