import { FC } from 'react'
import { Key } from '../../../config/componentTypes'
import './a-rectangle.scss'

interface IProps {
    rectConfig:{
        propValue: string,
        title: string | undefined,
        style: Record<Key, any>
    }
}

const ARectAngle: FC<IProps> = ({ rectConfig }) => {

    const { style, propValue, title } = rectConfig
    return (
        <div
            title={title}
            style={style}
            className="a-rect-angle">
            {propValue}
        </div>
    )
}

export default ARectAngle