import { FC } from 'react'
import { Key } from '../../../config/componentTypes'
// import { useDispatch } from 'react-redux'
// import { changeTextContent } from '../../../store/actionCreater'
import './a-text.scss'


export interface textConfig {
    propValue: string | number
    title: string | undefined,
    style: Record<Key, any>
}

const AText: FC<{ textConfig: textConfig }> = ({ textConfig }) => {
    // const dispatch = useDispatch()
    const { style, propValue, title } = textConfig

    // const handleInput = (e: any) => {
    //     const value = e.target.innerText
    //     dispatch(changeTextContent(value))
    // }

    return (
        // contentEditable='true' suppressContentEditableWarning onInput={handleInput} 
        <div
            title={title}
            style={style}
            className="a-text">
            {propValue}
        </div>
    )
}

export default AText