import { FC } from 'react'

import AText from '../../widget/a-text/AText'
import AButton from '../../widget/a-button/AButton'
import AImage from '../../widget/a-image/AImage'
import ARectAngle from '../../widget/a-rectangle/ARectAngle'
import AInput from '../../widget/a-input/AInput'
import { WidgetType } from '../../../config/componentList'
import { Widget } from '../../../config/componentTypes'
import valid from '../../../utils/schema'

import AButtonSchema from '../../widget/a-button/schema.json'
import ATextSchema from '../../widget/a-text/schema.json'
import AImageSchema from '../../widget/a-image/schema.json'
import ARectAngleSchema from '../../widget/a-rectangle/schema.json'
import AInputSchema from '../../widget/a-input/schema.json'


export interface IBaseProps {
    widget: Widget
}

interface IProps extends IBaseProps { }

const BaseComponent: FC<IProps> = (props) => {
    const { widget } = props

    switch (widget.type) {
        case WidgetType.AButton:
            const buttonConfig = {
                propValue: widget.propValue,
                title: widget.title,
                style: widget.style
            }
            try {
                valid(AButtonSchema, buttonConfig)
                return <AButton buttonConfig={buttonConfig} />
            } catch (err) {
                console.log(err)
                return null
            }
        case WidgetType.AText:

            const textConfig = {
                propValue: widget.propValue,
                title: widget.title,
                style: widget.style
            }
            try {
                valid(ATextSchema, textConfig)
                return <AText textConfig={textConfig} />
            } catch (err) {
                console.log(err)
                return null
            }

        case WidgetType.AImage:
            const imageConfig = {
                title: widget.title,
                url: widget.url,
                style: widget.style
            }
            try {
                valid(AImageSchema, imageConfig)
                return <AImage imageConfig={imageConfig} />
            } catch (err) {
                console.log(err)
                return null
            }
        case WidgetType.ARectangle:
            const rectConfig = {
                title: widget.title,
                propValue: widget.propValue,
                style: widget.style
            }
            try {
                valid(ARectAngleSchema, rectConfig)
                return <ARectAngle rectConfig={rectConfig} />
            } catch (err) {
                console.log(err)
                return null
            }
        case WidgetType.AInput:
            const inputConfig = {
                placeholder: widget.placeholder,
                style: widget.style
            }
            try {
                valid(AInputSchema, inputConfig)
                return <AInput inputConfig={inputConfig} />
            } catch (err) {
                console.log(err)
                return null
            }
        default:
            return null
    }
}

export default BaseComponent