import { FC, useEffect, useState } from 'react'
import { Table, Button, Input, Spin } from 'antd'
import { addPage, delPage, generateJSON, genertaeCode, getPages, publishProject } from '../../api'
import { useDispatch, } from 'react-redux'
import { Key, Widget } from '../../config/componentTypes'
import { findPageIndex, showError, showSuccess, storage } from '../../utils'
import { Link } from 'react-router-dom'
import { savePages, switchComponentData } from '../../store/actionCreater'
import AModal from '../../components/common/modal/AModal'
import { SettingOutlined, ArrowLeftOutlined } from '@ant-design/icons';
import './page-manager.scss'
import { BASE_URL, ONLINE } from '../../api/request'



export enum PageType {
    pageUrl = "pageUrl",
    description = "description",
    pageName = "pageName"
}

type Columns = Array<{
    title: string,
    dataIndex: string,
    key: string,
    render?: (...arg: any[]) => JSX.Element
}>

export interface PageItem {
    key?: Key,
    id?: string,
    pageUrl: string,
    pageName: string,
    description: string,
    componentData: Widget[]
}
const PageManager: FC = () => {

    const dispatch = useDispatch()
    const token = storage.get('token')

    const { TextArea } = Input

    const [tableData, setTableData] = useState<Array<PageItem>>([])
    useEffect(() => {
        const widthKey = (pages: Array<PageItem>) => {
            pages.forEach((page, index) => {
                page.key = index
            })
        }

        getPages().then(res => {
            const pages = res.data.data.pages as Array<PageItem>
            widthKey(pages)
            setTableData(pages)
            dispatch(savePages(pages))
        })
    }, [token, dispatch])

    const handleEditPage = (record: PageItem) => () => {
        const pageId = record.id as string
        console.log(pageId)
        storage.set('pageId', pageId)
        const componentData = record.componentData
        dispatch(switchComponentData(componentData))
    }

    const handleDelPage = (record: PageItem) => async () => {
        const pageId = record.id as string
        const result = await delPage(pageId)
        const message = result.data.message
        if (result.data.status === 200) {
            setTableData(pages => {
                const index = findPageIndex(pages, pageId)
                const newPages = [...pages]
                newPages.splice(index, 1)
                return newPages
            })
            showSuccess(message)
        } else {
            showError(message)
        }
    }

    const handleGererateJSON = () => async () => {
        const result = await generateJSON()
        const message = result.data.message
        if (result.data.status !== 200) {
            return showError(message)
        } else {
            window.location.href = BASE_URL + 'pageJson'
        }
    }

    const columns: Columns = [

        {
            title: '',
            dataIndex: 'pageName',
            key: 'age',
        },
        {
            title: '',
            dataIndex: 'pageUrl',
            key: 'name',
        },
        {
            title: '',
            dataIndex: "description",
            key: 'address',
        },
        {
            title: '',
            key: 'action',
            dataIndex: 'action',
            render(_, record: PageItem) {
                return (
                    <div style={{
                        display: 'flex',
                        justifyContent: 'space-evenly'
                    }}>
                        <Link to="/work">
                            <Button
                                onClick={handleEditPage(record)}
                                className="actions "
                            ></Button>
                        </Link>
                        <Button
                            onClick={handleDelPage(record)}
                            className="actions"
                        ></Button>
                        <Button
                            onClick={handleGererateJSON()}
                            className="actions"
                        >JSON</Button>
                    </div>
                )
            }
        },
    ];

    const handleAddPage = () => {
        showModal()
    }

    const [isModalVisible, setIsModalVisible] = useState<boolean>(false);
    const showModal = () => {
        setIsModalVisible(true);
    };
    const handleOk = async () => {
        const result = await addPage(pageInfo)
        
        if (result.data.status === 200) {
            //@ts-ignore
            const id = result.data.page.id
            console.log(id)
            setTableData(pages => {
                const page = { ...pageInfo, key: pages.length,  id }
                return [...pages, page]
            })
            showSuccess(result.data.message)
        } else {
            showError(result.data.message)
        }
        setIsModalVisible(false);
    };
    const handleCancel = () => {
        setIsModalVisible(false);
    };

    const [pageInfo, setPageInfo] = useState<PageItem>({
        pageUrl: '',
        description: '',
        pageName: '',
        componentData: []
    })

    const handleInputChange = (type: PageType) => (e: any) => {
        const value = e.target.value
        setPageInfo((pageInfo) => {
            const info = pageInfo
            info[type] = value
            return info
        })
    }


    const [loading, setLoading] = useState<boolean>(false)
    const [showLink, setShowLink] = useState<boolean>(false)

    const handlePulbish = async () => {
        setLoading(true)
        const result = await publishProject()
        const message = result.data.message

        if (result.data.status === 200) {
            setLoading(false)
            showSuccess(message)
        } else {
            setLoading(false)
            showError(message)
        }
    }

    const handleGenerate = async () => {
        const result = await genertaeCode()
        const message = result.data.message

        if (result.data.status === 200) {
            showSuccess(message)
        } else {
            showError(message)
        }
    }

    return (
        <Spin size="large" spinning={loading}>
            <div className="page-manager">
                <div className="header">
                    <Button
                        className='link'
                        icon={<SettingOutlined />}
                        onClick={() => setShowLink(true)}
                    ></Button>
                    <Link to='/home'>
                        <Button
                            className='back'
                            icon={<ArrowLeftOutlined />}
                            onClick={() => setShowLink(true)}
                        ></Button>
                    </Link>
                </div>
                <Table
                    className="table"
                    pagination={false}
                    columns={columns}
                    dataSource={tableData}
                />
                <div className="options">
                    <div onClick={handleAddPage} className="add-pages">
                        
                    </div>
                    <div onClick={handlePulbish} className="publish">
                        
                    </div>
                    <div onClick={handleGenerate} className="publish">
                        
                    </div>
                </div>

                <AModal
                    title=""
                    visible={isModalVisible}
                    onOk={handleOk}
                    onCancel={handleCancel}
                    okText=""
                    cancelText=""
                >
                    <Input
                        onChange={handleInputChange(PageType.pageName)}
                        style={{
                            marginBottom: 10
                        }} placeholder="" />
                    <Input
                        onChange={handleInputChange(PageType.pageUrl)}
                        style={{
                            marginBottom: 10
                        }} placeholder="" />
                    <TextArea
                        onChange={handleInputChange(PageType.description)}
                        rows={4} placeholder="" />
                </AModal>
                <AModal
                    title=""
                    onCancel={() => setShowLink(false)}
                    onOk={() => setShowLink(false)}
                    okText={''}
                    cancelText={''}
                    visible={showLink}
                    width={250}
                >
                    <p><a target='_blank' rel="noopener noreferrer" href={ONLINE}></a></p>
                    <p><a href={BASE_URL + 'download'}></a></p>
                </AModal>
            </div>
        </Spin>
    )
}

export default PageManager