import { FC } from 'react'
import { Key } from '../../../config/componentTypes'
import './a-image.scss'

export interface imageConfig {
    url: string,
    title: string | undefined,
    style: Record<Key, any>
}


const AImage: FC<{
    imageConfig: imageConfig
}> = ({ imageConfig }) => {
    const { style, url, title } = imageConfig

    const handleImageDown = (e: any) => {
        e.preventDefault()
    }

    return (
        <div className="a-image">
            <img
                title={title}
                onMouseDown={handleImageDown}
                style={style}
                src={url} alt={title}
            />
        </div>
    )
}

export default AImage